import React from 'react';
import { getHref } from '../data/tags';

const renderStringTag = (tag) => <a href={getHref(tag)}>{tag}</a>;

const renderCountTag = ({ name, count }) => <a href={getHref(name)}>{name}<span>{count}</span></a>;

export const TagArea = ({ tags }) => {
  return tags ? (
    <div className='tag-area'>
      { tags.map((tag, i) => <Tag value={tag} key={`tag-${i}`} />) }
    </div>
  ) : null;
};

const Tag = ({ value }) => {
  if (typeof value === 'string') return renderStringTag(value);
  return renderCountTag(value);
};

export default Tag;



