import React, { Component } from 'react';
import Timeline, { TimelineCard } from './timeline';
import { TagArea } from './tag';
import education from '../data/education.json';

const EducationCard = ({ title, subtitle, logo, date, description, inverted, tags }) => {
  return (
    <TimelineCard inverted={inverted}>
      <div className='education-card'>
        <div className='education-card__logo'>
          <img src={logo} alt=' ' />
        </div>
        <div className='education-card__info'>
          <h1>{title}</h1>
          <h3>{subtitle}</h3>
          <h3>{date}</h3>
          <p>{description}</p>
        </div>
      </div>
      <TagArea tags={tags} />
    </TimelineCard>
  );
};

class Education extends Component {
  render() {
    return (
      <section className="education" id='education'>
        <h1 className='section-title'>Education</h1>
        <div className='content'>
          <Timeline>
            { education.map((edu, i) => <EducationCard key={i} {...edu} />) }
          </Timeline>
        </div>
      </section>
    );
  }
}

export default Education;



